<script>
	import { createEventDispatcher } from 'svelte'
	import { fade } from 'svelte/transition'
	import Icon from '@iconify/svelte'
	import Spinner from '../../components/misc/Spinner.svelte'
	import { showKeyHint } from '../../stores/app/misc'

	const dispatch = createEventDispatcher()

	export let id = null
	export let title = ''

	/** @type {string | null} */
	export let label = null
	export let key = null
	export let icon = null
	export let svg = null
	export let disabled = false
	export let onclick = null
	export let loading = false
	export let active = false
	export let buttons = null
	export let type = null
	export let style = ''

	let subButtonsActive = false
</script>

<button
	{id}
	aria-label={title}
	class="primo-button"
	class:primo={type === 'primo'}
	class:active
	class:has-subbuttons={buttons}
	class:has-icon-button={!label && icon}
	{style}
	in:fade={{ duration: 200 }}
	{disabled}
	on:click={() => {
		subButtonsActive = !subButtonsActive
		onclick ? onclick() : dispatch('click')
	}}
>
	{#if icon || svg}
		{#if key}
			<span class="key-hint" class:active={$showKeyHint} aria-hidden>
				&#8984;{key.toUpperCase()}
			</span>
		{/if}
		{#if loading}
			<Spinner />
		{:else if label && svg}
			<div class="svg">
				{@html svg}
			</div>
			<span class="label">{label}</span>
		{:else if label && icon}
			<Icon {icon} />
			<span class="label">{label}</span>
		{:else if svg}
			<div class="svg">
				{@html svg}
			</div>
		{:else if icon}
			<Icon {icon} />
		{/if}
	{:else}
		<slot>
			<span>{label}</span>
		</slot>
	{/if}
</button>

